import React, { useState, useEffect } from 'react';

import Head from 'next/head'
import LangPopUp from '../components/LangPopUp';
import Welcome from '../components/Welcome';
import About from '../components/About';
import Work from '../components/Work';
import Contact from '../components/Contact';
import { getData, storeLang, getLang } from '../utils/lang';
import Quotes from '../components/Quotes';
import Navbar from '../components/Navbar';

const Home = () => {
  const [lang, setLang] = useState(false);

  useEffect(() => {
    // alert("Website Under Construction!");
    if (typeof window.localStorage.lang !== "undefined") {
      setLang(window.localStorage.lang);
    }
  }, []);

  const handleLang = (lang) => {
    setLang(lang);
    storeLang(lang);
  }

  const data = getData(lang);
  return (
    <div className="container" id="top">
      <Head>
        <title>(Under Construction) Ashraf El ghinoussi's Portfolio</title>
        <link rel="icon" href="/favicon.ico" />
      </Head>
      {(1 ? "" : <LangPopUp setLang={handleLang} data={data["language"]} />)}
      <Welcome setLang={handleLang} data={data["welcome"]} />
      <Navbar />
      <About />
      <Work />
      <Contact />
      <Quotes />
      <style jsx global>{`
      html,
      body {
        padding: 0;
        margin: 0;
        font-family: 'Raleway', -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen,
          Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
                    font-weight: 400;
      }

      * {
        box-sizing: border-box;
      }
      
    `}</style>
    </div>
  );
}

export default Home
